* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #eee;
}

.title {
  margin: 20px 0;
  text-align: center;
}

.title a {
  font-size: 30px;
  cursor: pointer;
  position: relative;
  z-index: 2;
  user-select:         none; /* CSS3          */
  -moz-user-select:    none; /* Firefox       */
  -webkit-user-select: none; /* Safari Chrome */
  -ms-user-select:     none; /* IE10以降      */
}

.items {
  margin: 0 auto;
  max-width: 1100px;
}

.items .size-cover {
  background-size: cover;
}

.items .contain {
  background-size: contain;
}

.items div,
.items a {
  position: relative;
  float: left;
  width: 100px;
  height: 100px;
  margin: 0 10px 10px 0;
  background-repeat: no-repeat;
  background-color: #fff;
  border-radius: 20px;
  display: block;
  cursor: pointer;
}

.items .selected {
  position: relative;
  width: 320px;
  height: 320px;
  cursor: auto;
  background-position: center center;
}

.items .selected .taInfo {
  position: relative;
  cursor: auto;
  display: none;
  width: 320px;
  height: 320px;
  background-color: #fff;
  opacity: .95;
  z-index: 2;
}

.items .selected .taInfo p:first-child {
  margin: 40px 0 30px;
}

.items .selected .taInfo p {
  position: relative;
  text-align: center;
  margin-top: 20px;
  line-height: 25px;
  font-size: 25px;
  color: #000;
}

.items .selected .taInfo p.taReset {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100px;
  height: 25px;
  font-size: 20px;
  margin: 0;
  border-radius: 20px;
  background-color: #ccc;
  cursor: pointer;
}

.items .selected .onlineInfo {
  position: relative;
  cursor: auto;
  display: none;
  width: 320px;
  height: 320px;
  background-color: #fff;
  opacity: .95;
  z-index: 2;
}

.items .selected .onlineInfo p {
  position: relative;
  text-align: center;
  margin-top: 25px;
  line-height: 25px;
  font-size: 25px;
  color: #000;
}

.items .tweet {
  position: relative;
  background-color: #55acee;
  background-image: url(/images/Twitter_logo_white.png);
  background-size: auto;
  background-position: center center;
}

.items p {
  position: absolute;
  text-align: center;
  color: #fff;
  width: 100%;
  bottom: 7px;
}

.items p.rankingCount {
  bottom: 0;
  right: 0;
  width: 40px;
  height: 25px;
  font-size: 18px;
  line-height: 25px;
  margin: 0;
  border-radius: 20px 0 0 0 / 20px 0 0 0;
  color: #000;
  background-color: #eee;
}

.items .count {
  position: relative;
  text-align: center;
  line-height: 100px;
  font-size: 20px;
}

.items .sorting {
  border: solid 3px #55acee;
}

a.birthday {
  display: none;
  position: relative;
  background-color: #DFEAD6;
}

.birthday p {
  top: 20px;
  line-height: 20px;
  color: #000;
}

a.sta4koma {
  position: relative;
  background-image: url(/images/sta4koma.png);
  background-size: 80%;
  background-position: center center;
}

.sta4koma p {
  color: #000;
}

a.stabook {
  position: relative;
  background-image: url(/images/stabook.png);
  background-position: center center;
}

a.statobashi {
  position: relative;
}

.statobashi p {
  color: #000;
}

.mode label {
  display: block;
  margin: 22px 0;
  text-align: center;
  line-height: 17px;
  font-size: 17px;
}

.modelabel.on {
  color: #ee55ac;
}

.modelabel.off {
  color: #55acee;
}

.sync {
  position: relative;
  text-align: center;
  line-height: 100px;
  font-size: 17px;
}

.updated {
  display: none;
  position: fixed;
  top: 20px;
  left: 20px;
  background-color: #000;
  color: #fff;
  padding: 10px;
  border-radius: 10px;
}

.arrow:after {
  border: solid transparent;
  content: ' ';
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-color: rgba(255, 255, 255, 0);
  border-width: 10px;
}

.arrowtweetleft:after {
  right: 100%;
  top: 50%;
  border-right-color: #55acee;
  margin-top: -10px;
}

.arrowtweettop:after {
  bottom: 100%;
  left: 50%;
  border-bottom-color: #55acee;
  margin-left: -10px;
}

.arrowcountleft:after {
  right: 100%;
  top: 50%;
  border-right-color: #fff;
  margin-top: -10px;
}

.arrowcounttop:after {
  bottom: 100%;
  left: 50%;
  border-bottom-color: #fff;
  margin-left: -10px;
}

.bullet {
  position: fixed;
  z-index: 1;
}

.clearboth {
  clear: both;
}

.footer {
  text-align: center;
}

.launchCounter {
  position: fixed;
  top: 50px;
  left: 20px;
  z-index: 2;
}

